.hero {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: -webkit-gradient(linear, left top, left bottom, from(#383c4a), to(#585c68));
  background: linear-gradient(to bottom, #383c4a, #585c68);
  height: 100vh;
  top: 0;
}
.hero .name {
  color: white;
  font-size: 3rem;
  text-transform: uppercase;
}
#typed-element {
  color: #5294e2;
  font-size: 2rem;
}
.typed-cursor {
  color: #7c818c;
  font-size: 2rem;
}
.discover {
  position: absolute;
  right: 5%;
  bottom: 5%;
}
.discover a {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  text-orientation: mixed;
  font-size: 19px;
}
.discover i {
  /* transform: rotate(90deg); */
  -webkit-animation: discover 1s ease infinite alternate;
          animation: discover 1s ease infinite alternate;
}
@-webkit-keyframes discover {
from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
to {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
}
}
@keyframes discover {
from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
to {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
}
}
@media screen and (max-width: 425px) {
