#notfound {
  position: relative;
  height: 100vh;
  background-color: #383c4a;
  padding: 0;
  margin: 0
}
#notfound .notfound {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.notfound {
  max-width: 520px;
  width: 100%;
  line-height: 1.4;
  text-align: center;
}
.notfound .notfound-404 {
  position: relative;
  height: 240px;
}
.notfound .notfound-404 h1 {
  font-family: "Montserrat", sans-serif;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  font-size: 252px;
  font-weight: 900;
  margin: 0px;
  color: #262626;
  text-transform: uppercase;
  letter-spacing: -40px;
  margin-left: -20px;
}
.notfound .notfound-404 h1 > span {
  text-shadow: -8px 0px 0px white;
}
.notfound .notfound-404 h3 {
  font-family: "Cabin", sans-serif;
  position: relative;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: white;
  margin: 0px;
  letter-spacing: 3px;
  padding-left: 6px;
}
.notfound h2 {
  font-family: "Cabin", sans-serif;
  font-size: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: white;
  margin-top: 0px;
  margin-bottom: 25px;
}
@media only screen and (max-width: 767px) {
.notfound .notfound-404 {
    height: 200px;
}
.notfound .notfound-404 h1 {
    font-size: 200px;
}
}
@media only screen and (max-width: 480px) {
.notfound .notfound-404 {
    height: 162px;
}
.notfound .notfound-404 h1 {
    font-size: 162px;
    height: 150px;
    line-height: 162px;
}
.notfound h2 {
    font-size: 16px;
}
}
* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    font-family: 'Red Hat Display', sans-serif;
}
h1, h2, h3, h4, h5, h6 {
    color: white;
}
a {
    color: white;
    text-decoration: none;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
}
a:hover {
    color: #5294e2;
}
button {
    background-color: #5294e2;
    color:white;
    border: none;
    border-radius: 50px;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
    padding: 10px 20px;
    font-family: 'Red Hat Display', sans-serif;
}
button:hover {
    background-color: white;
    color: #5294e2;
    cursor: pointer;
}
input, textarea {
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #5294e2;
    color: white;
    text-align: center;
    -webkit-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}
input:focus, textarea:focus{
    outline:none;
    border-bottom: 1px solid white;
}
::-webkit-input-placeholder {
    color: white;
    opacity: 0.4;
    text-align: center;
}
::-moz-placeholder {
    color: white;
    opacity: 0.4;
    text-align: center;
}
::-ms-input-placeholder {
    color: white;
    opacity: 0.4;
    text-align: center;
}
::placeholder {
    color: white;
    opacity: 0.4;
    text-align: center;
}
.title span {
    background-image: linear-gradient(to top, #5294e2 40%, transparent 0px);
